body, html {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background-color: #2a2a2a;
  height: 100%;
}

#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.App {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

main {
  flex-grow: 1;
}

nav a.active {
  border-bottom: 2px solid #f7931e;
}
