.contact {
  padding: 2rem;
  background-color: #2a2a2a;
  color: white;
  text-align: center;
}

.contact h1 {
  font-size: 2.5rem;
  margin-bottom: 1.5rem;
}

.contact p {
  font-size: 1.25rem;
  margin-bottom: 1rem;
  line-height: 1.6;
}

.contact a {
  color: #f7931e;
  text-decoration: none;
}

.contact a:hover {
  text-decoration: underline;
}
