.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  background-color: #2a2a2a; /* Same background color as Hero and Footer */
}

.navbar-logo a {
  display: flex;
  align-items: center;
}

.navbar-logo img {
  height: 50px; /* Adjust the height as needed */
}

.navbar-links a {
  margin: 0 1rem;
  color: white;
  text-decoration: none;
  font-size: 1rem;
}

.navbar-links a:hover {
  color: #f7931e;
}
