.hero {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 58vh;
  padding: 5rem 5rem;
  background-color: #2a2a2a;
  color: white;
}

.hero-text {
  max-width: 60%;
}

.hero h1 {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.hero p {
  font-size: 1.25rem;
  margin-bottom: 2rem;
}

.hero button {
  padding: 0.75rem 2rem;
  font-size: 1rem;
  background-color: #f7931e;
  border: none;
  color: white;
  cursor: pointer;
}

.hero button:hover {
  background-color: #e5831a;
}

.hero-image img {
  max-width: 100%;
  height: auto;
  border-radius: 10px;
}
